"use server";

import prisma from "../db/db";

// Bring all users
export async function getAllUsers() {
  try {
    const users = await prisma.user.findMany();
    return users;
  } catch (error) {
    console.error("Error fetching users:", error);
    throw new Error("Error occured while fetching users.");
  }
}

// Bring specific user
export async function getUserProfile(userId: number) {
  try {
    const userProfile = await prisma.user.findUnique({
      where: { id: userId },
    });

    if (!userProfile) {
      throw new Error("User not found.");
    }

    return userProfile;
  } catch (error) {
    console.error(`Error fetching user with ID ${userId}:`, error);
    throw new Error("Error occured while fetching user profile.");
  }
}

// Subscribe to the newsletter
export async function addToNewsLetter(email: string) {
  try {
    const existing = await prisma.newsletterSubscriber.findUnique({
      where: { email },
    });

    if (existing) {
      throw new Error("This email is already subscribed to the newsletter.");
    }

    const subscriber = await prisma.newsletterSubscriber.create({
      data: { email },
    });

    return subscriber;
  } catch (error) {
    console.error(
      `Error adding user to the newsletter subscriber collection with email ${email}:`,
      error
    );
    throw new Error(
      "Error occurred while adding user to the newsletter subscriber collection."
    );
  }
}
